/*
** website navbar links
*/
#navbar {
  background-color: #fff;
  display: block; }
  #navbar > div > ul {
    margin: 0px;
    padding: 0px; }
    #navbar > div > ul > div {
      display: inline-block; }
      #navbar > div > ul > div > li {
        display: inline-block;
        position: relative;
        transition: all 0.5s ease-in-out; }
        #navbar > div > ul > div > li:hover {
          z-index: 11; }
        #navbar > div > ul > div > li:hover ul {
          top: 80px !important; }
        #navbar > div > ul > div > li > div {
          transition: all 0.5s ease-in-out; }
          #navbar > div > ul > div > li > div.hasSub::before {
            content: '';
            background: #fdb414;
            position: absolute;
            top: -20px;
            left: -35px;
            right: -35px;
            display: block;
            height: 20px;
            opacity: 0;
            transition: all 0.5s ease-in-out; }
          #navbar > div > ul > div > li > div.hasSub::after {
            position: absolute;
            right: -54px;
            top: -20px;
            width: 0;
            content: '';
            height: 0;
            border-style: solid;
            border-width: 20px 0 0 20px;
            border-color: transparent transparent transparent #c08709;
            transition: all 0.5s ease-in-out;
            opacity: 0; }
          #navbar > div > ul > div > li > div.hasSub:hover ul {
            opacity: 1 !important; }
          #navbar > div > ul > div > li > div.hasSub:hover .bottom {
            opacity: 1 !important; }
          #navbar > div > ul > div > li > div:hover {
            background: #fdb414; }
            #navbar > div > ul > div > li > div:hover::after, #navbar > div > ul > div > li > div:hover::before {
              opacity: 1 !important; }
          #navbar > div > ul > div > li > div:hover a {
            color: #FFF !important; }
          #navbar > div > ul > div > li > div > a {
            padding: 40px 35px;
            color: #182752;
            display: inline-block;
            text-decoration: none;
            font-size: 23px;
            font-family: HelveticaNeue-Thin;
            font-weight: normal;
            line-height: normal;
            transition: all 0.5s ease-in-out; }
        #navbar > div > ul > div > li ul {
          position: absolute;
          top: 80px;
          left: 0px;
          width: 100%;
          opacity: 0;
          transition: all 0.5s ease-in-out;
          padding-bottom: 20px;
          background: #fdb414; }
          #navbar > div > ul > div > li ul::after {
            z-index: -1;
            background: #fdb414;
            left: -35px;
            right: -35px;
            bottom: 0;
            margin: auto;
            content: '';
            position: absolute;
            top: -100px; }
          #navbar > div > ul > div > li ul li {
            display: block;
            background-color: #fdb414;
            position: relative; }
            #navbar > div > ul > div > li ul li::before {
              position: absolute;
              left: 0px;
              top: 0px;
              bottom: 0;
              margin: auto;
              content: '';
              width: 15px;
              height: 13px;
              background: url(/images/navbar/right-arrow.webp) no-repeat; }
            #navbar > div > ul > div > li ul li a {
              padding: 5px 0px 0px 10px;
              margin: 0px;
              color: #FFF;
              font-size: 18px;
              margin-left: 20px;
              display: inherit; }
              #navbar > div > ul > div > li ul li a:hover {
                color: #2249a1 !important; }
          #navbar > div > ul > div > li ul img {
            width: 100%; }
        #navbar > div > ul > div > li .bottom {
          transition: all 0.5s ease-in-out;
          left: -35px;
          right: -35px;
          top: 100%;
          position: absolute;
          margin: auto; }
  #navbar.fixed-top .subNavLinks {
    top: -300px; }

